.person-card {
    margin-bottom: 20px;
  }
  
  .person-details {
    display: flex;
    align-items: center;
  }
  
  .profile-picture {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 10px;
    border: 2px solid #ccc;
  }
  
  .profile-picture img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .person-info {
    display: flex;
    flex-direction: column;
  }
  